import React from "react";
import { Navigate } from "react-router-dom";
import { Button, Checkbox, FormControl, FormControlLabel, FormGroup, FormLabel, Grid, TextField } from "@mui/material";
import { useLogin } from "features/Auth/lib/useLogin";
import { selectCaptchaUrl } from "features/Auth/model/auth.selectors";
import { useSelector } from "react-redux";

export const Login = () => {

  const { formik, isLoggedIn } = useLogin();

  const captchaUrl = useSelector(selectCaptchaUrl);

  if (isLoggedIn) {
    return <Navigate to={"/"} />;
  }

  return (
    <Grid container justifyContent="center">
      <Grid item xs={4}>
        <form onSubmit={formik.handleSubmit}>
          <FormControl>
            <FormLabel>
              <p>
                To log in get registered{" "}
                <a href={"https://social-network.samuraijs.com/"} target={"_blank"}>
                  here
                </a>
              </p>
              <p>or use common test account credentials:</p>
              <p> Email: free@samuraijs.com</p>
              <p>Password: free</p>
            </FormLabel>
            <FormGroup>
              <TextField label="Email" margin="normal" {...formik.getFieldProps("email")} />
              {formik.errors.email ? <div style={{ color: "red" }}>{formik.errors.email}</div> : null}
              <TextField type="password" label="Password" margin="normal" {...formik.getFieldProps("password")} />
              {formik.errors.password ? <div style={{ color: "red" }}>{formik.errors.password}</div> : null}
              <FormControlLabel
                label={"Remember me"}
                control={<Checkbox {...formik.getFieldProps("rememberMe")} checked={formik.values.rememberMe} />}
              />
              <Button type={"submit"} variant={"contained"} color={"primary"}>
                Login
              </Button>
              {!!captchaUrl &&
                <>
                  <img src={captchaUrl} alt="Captcha" />
                  <TextField label="Enter the symbols from the picture" type="text"
                             margin="normal" {...formik.getFieldProps("captcha")} />
                  {formik.errors.captcha ?
                    <div style={{ color: "red" }}>{formik.errors.captcha}</div>
                    :
                    <div style={{ color: "red" }}>{formik.errors.captcha}</div>
                  }
                </>
              }
            </FormGroup>
          </FormControl>
        </form>
      </Grid>
    </Grid>
  );
};

export type LoginParamsType = {
  email: string;
  password: string;
  rememberMe: boolean;
  captcha: string;
};


